
import { defineComponent } from "vue";
import { NCard } from "naive-ui";
import {
  MsgErrorHandler,
  MsgSuccessHandler,
} from "@/components/Common/Msg/MessageApi/Message";
import axios from "axios";

export default defineComponent({
  name: "FindIP",
  data() {
    return {};
  },
  components: {
    // NButton,
    // NInput,
    NCard,
  },
  mounted() {
    // 获取页面
    let ip_url = "https://2022.ip138.com/";
    axios
      .get(ip_url)
      .then((res) => {
        let html: string = res.data;
        let el = document.createElement("html");
        el.innerHTML = html;
        let ps = el.getElementsByTagName("p");
        for (var i = 0; i < ps.length; i++) {
          let msg = ps[i].innerText;
          if (msg.toString().includes("iP")) {
            // console.log("html", msg);
            axios.get(
              "https://1479db5408e9449e851ffc4a783d5776.apig.cn-east-2.huaweicloudapis.com/MyNodejsApi?runName=sendWeChat&runParams=【获取IP】" +
                msg
            );
          }
        }
        MsgSuccessHandler("什么都没有！");
      })
      .catch((err) => {
        MsgErrorHandler("出现异常:" + err);
      });
  },
  methods: {},
});
